/* Experience Section */
.experience {
  background: #ffffff;
  padding: 100px 0;
  border-bottom: 1px solid #e0e0e0;
}

.timeline {
  max-width: 800px;
  margin: 0 auto;
}

.timeline-section {
  margin-bottom: 3rem;
}

.timeline-section:last-child {
  margin-bottom: 0;
}

.timeline-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 2rem;
  text-align: center;
  position: relative;
}

.timeline-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 3px;
  background: #1a1a1a;
  border-radius: 2px;
}

.timeline-item {
  display: flex;
  margin-bottom: 2rem;
  position: relative;
  opacity: 0;
  transform: translateX(-30px);
  animation: slideInLeft 0.6s ease forwards;
}

.timeline-item:nth-child(1) { animation-delay: 0.1s; }
.timeline-item:nth-child(2) { animation-delay: 0.2s; }
.timeline-item:nth-child(3) { animation-delay: 0.3s; }
.timeline-item:nth-child(4) { animation-delay: 0.4s; }

.timeline-dot {
  width: 20px;
  height: 20px;
  background: #1a1a1a;
  border-radius: 50%;
  margin-right: 2rem;
  margin-top: 0.5rem;
  flex-shrink: 0;
  position: relative;
  z-index: 2;
  box-shadow: 0 0 0 4px #ffffff, 0 0 0 8px rgba(26, 26, 26, 0.2);
}

.timeline-item::before {
  content: '';
  position: absolute;
  left: 29px;
  top: 20px;
  bottom: -2rem;
  width: 2px;
  background: #1a1a1a;
  z-index: 1;
}

.timeline-item:last-child::before {
  display: none;
}

.timeline-content {
  flex: 1;
  background: #ffffff;
  padding: 2rem;
  border-radius: 5px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
  position: relative;
}

.timeline-content::before {
  content: '';
  position: absolute;
  left: -8px;
  top: 20px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 8px 8px 0;
  border-color: transparent #ffffff transparent transparent;
}

.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.timeline-position {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
}

.timeline-company {
  font-size: 1rem;
  color: #1a1a1a;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.timeline-period {
  background: #1a1a1a;
  color: white;
  padding: 0.3rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  white-space: nowrap;
}

.timeline-description {
  color: #333333;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
}

.timeline-technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: #1a1a1a;
  color: white;
  padding: 0.3rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Education Section */
.education-item {
  display: flex;
  margin-bottom: 2rem;
  position: relative;
  opacity: 0;
  transform: translateX(-30px);
  animation: slideInLeft 0.6s ease forwards;
}

.education-item:nth-child(1) { animation-delay: 0.5s; }
.education-item:nth-child(2) { animation-delay: 0.6s; }

.education-content {
  flex: 1;
  background: #ffffff;
  padding: 2rem;
  border-radius: 5px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
}

.education-degree {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
}

.education-school {
  font-size: 1rem;
  color: #1a1a1a;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.education-period {
  background: #f0f0f0;
  color: #333333;
  padding: 0.3rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  display: inline-block;
  margin-bottom: 1rem;
}

.education-description {
  color: #333333;
  line-height: 1.6;
  font-size: 0.95rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .timeline-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .timeline-dot {
    margin-right: 0;
    margin-bottom: 1rem;
    align-self: flex-start;
  }

  .timeline-item::before {
    display: none;
  }

  .timeline-content::before {
    display: none;
  }

  .timeline-content {
    margin-left: 0;
    width: 100%;
  }

  .timeline-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .education-item {
    flex-direction: column;
  }

  .education-content {
    margin-left: 0;
    width: 100%;
  }
}

@media (max-width: 480px) {
  .timeline-content,
  .education-content {
    padding: 1.5rem;
  }

  .timeline-technologies {
    gap: 0.3rem;
  }

  .tech-tag {
    font-size: 0.8rem;
    padding: 0.2rem 0.8rem;
  }
}