/* Hero Section */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  background: #ffffff;
  overflow: hidden;
}

.hero-content {
  position: relative;
  z-index: 3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.hero-text {
  color: #1a1a1a;
}

.hero-title {
  font-size: 3rem;
  font-weight: 300;
  line-height: 1.3;
  margin-bottom: 1.5rem;
  opacity: 0;
  animation: fadeInUp 1s ease 0.5s forwards;
  letter-spacing: 1px;
}

.highlight {
  color: #1a1a1a;
  position: relative;
  font-weight: 600;
}

.highlight::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: #1a1a1a;
  transform: scaleX(0);
  animation: underlineExpand 1s ease 1.5s forwards;
}

@keyframes underlineExpand {
  to {
    transform: scaleX(1);
  }
}

.hero-subtitle {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  opacity: 0;
  animation: fadeInUp 1s ease 0.8s forwards;
  color: #666666;
  line-height: 1.6;
}

.typing-container {
  margin-bottom: 2rem;
  min-height: 2rem;
}

.typing-text {
  display: inline-block;
  min-height: 1.5em;
  font-size: 1.2rem;
  color: #1a1a1a;
  opacity: 0;
  animation: fadeInUp 1s ease 1.2s forwards;
}

.cursor {
  animation: blink 1s infinite;
  color: #1a1a1a;
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

.hero-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
  opacity: 0;
  animation: fadeInUp 1s ease 1.5s forwards;
}

.hero-location,
.hero-role {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #666666;
  font-size: 1rem;
}

.hero-location i,
.hero-role i {
  color: #1a1a1a;
  width: 16px;
}

.hero-actions {
  display: flex;
  gap: 1rem;
  opacity: 0;
  animation: fadeInUp 1s ease 1.8s forwards;
}

.hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 300px;
  height: 300px;
  margin: 0 auto;
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  border: 5px solid #f0f0f0;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.profile-image:hover {
  transform: scale(1.05);
}

.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
  opacity: 0;
  animation: fadeInUp 1s ease 2s forwards;
}

.scroll-arrow {
  color: #1a1a1a;
  font-size: 1.5rem;
  animation: bounce 2s infinite;
  cursor: pointer;
}

.scroll-arrow:hover {
  color: #000000;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }

  .hero-title {
    font-size: 2.5rem;
  }

  .hero-subtitle {
    font-size: 1.1rem;
  }

  .hero-image {
    width: 250px;
    height: 250px;
  }

  .hero-actions {
    justify-content: center;
    flex-wrap: wrap;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }

  .hero-image {
    width: 200px;
    height: 200px;
  }

  .hero-actions {
    flex-direction: column;
    align-items: center;
  }
}