/* About Section */
.about {
  background: #ffffff;
  padding: 100px 0;
  border-bottom: 1px solid #e0e0e0;
}

.about-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 4rem;
  align-items: start;
}

.about-text {
  background: #ffffff;
  padding: 3rem;
  border: 1px solid #e0e0e0;
  position: relative;
  overflow: hidden;
}

.about-text::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: #1a1a1a;
}

.about-description {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #333333;
  margin-bottom: 2.5rem;
}

.skills-section {
  margin-top: 2rem;
}

.skills-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.skills-title::before {
  content: '💻';
  font-size: 1.2rem;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.skill-item {
  background: #1a1a1a;
  color: white;
  padding: 0.8rem 1rem;
  border-radius: 5px;
  text-align: center;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
  cursor: default;
}

.skill-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(26, 26, 26, 0.3);
}

.achievements-section {
  margin-top: 2rem;
}

.achievements-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.achievements-title::before {
  content: '🏆';
  font-size: 1.2rem;
}

.achievements-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.achievement-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 5px;
  border-left: 4px solid #1a1a1a;
  transition: all 0.3s ease;
}

.achievement-item:hover {
  background: #e9ecef;
  transform: translateX(5px);
}

.achievement-icon {
  font-size: 1.2rem;
  flex-shrink: 0;
}

.achievement-text {
  font-size: 0.95rem;
  color: #333333;
  font-weight: 500;
}

.about-stats {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.stat-item {
  text-align: center;
  padding: 2rem;
  background: #f8f9fa;
  border-radius: 5px;
  border: 1px solid #e0e0e0;
  transition: all 0.3s ease;
}

.stat-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
  display: block;
}

.stat-label {
  font-size: 1rem;
  color: #666666;
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .about-text {
    padding: 2rem;
  }

  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }

  .about-stats {
    flex-direction: row;
    gap: 1rem;
  }

  .stat-item {
    padding: 1.5rem;
  }

  .stat-number {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .about-stats {
    flex-direction: column;
  }

  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}